import React from "react";
import Bounce from 'react-reveal/Bounce';
import './Faq.scss'

export class Faq extends React.Component {

  render() {
    return (
      <Bounce left>
        <div>
          <main className="content-home content-login content-product-details content-shopping-bag">
            <div className="container">
              <div className="std"><div id="content_container">
                <div id="main_content">
                  <div className="wrapper">
                    <div id="content_single">
                      <div className="inner-wrapper">
                        <h1 className="align-left no-margin">FAQ</h1>
                        <div>
                          <h5>How to redeem samples?</h5>
                          <p><img className="img-fluid" src="https://sssgprd.s3.amazonaws.com/wysiwyg/FAQ_1.png" alt="" /></p>
                        </div>
                        <div className="col-half-left">
                          <h5>How can I change my shipping address?</h5>
                          <p>By default, the last used shipping address will be saved into to your Sample Store account. When you are checking out your order, the default shipping address will be displayed and you have the option to amend it if you need to.</p>
                          <h5>How do I activate my account?</h5>
                          <p>The instructions to activate your account will be sent to your email once you have submitted the registration form. If you did not receive this email, your email service providers mailing software may be blocking it. You can try checking your junk / spam folder or contact us at help@samplestore.com</p>
                          <h5>What do you mean by points? How do I earn it?</h5>
                          <p>Because you are important to us, we want to know what you think about the products. As an added value, every time you rate the products you earn points which go straight to your account. 1 point are added to your account for every review that you give. You will need those points in order to redeem the sample products. So keep rating the products to keep earning points!</p>
                          <h5>Why is there a checkout limit? / What are all the checkout limits?</h5>
                          <p>Sample Store is a popular spot and gets lots of shoppers at a time. These limits are in place to make sure everyone has a good time trying and purchasing their products. So...</p>
                          <p>- Each member is entitled to only one (1) sample order every day.</p>
                          <p>- Each member is entitled to one (1) bundle of sample for each product.</p>
                          <p>- Your account must have sufficient points before you can checkout the sample products.</p>
                          <p>- Kindly clear all pending payments before another checkout.</p>
                          <h5>How can I track my orders &amp; payment?</h5>
                          <p>After logging into your account, the status of your checkout history can be found under Order History. For orders via registered postage, a tracking number (article tracking number) will be given to you after the receipt given from <a href="http://www.singpost.com.sg" target="_blank" rel="noopener noreferrer">Singapore Post Limited (SingPost)</a>.</p>
                          <h5>How do I cancel my orders before I make a payment?</h5>
                          <p>After logging into your account, go to your Shopping Cart. Here, you will be able to make payment or cancel your order. Note: We cannot give refunds once payment is verified.</p>
                          <h5>How long will it take for my order to arrive after I make payment?</h5>
                          <p>Members who ship their orders within Singapore should expect to receive their orders within five (5) to ten (10) working days upon payment verification depending on the volume of orders received.</p>
                          <p>If you experience delays in receiving your order, contact us immediately and we will help to confirm the status of your order.</p>
                          <h5>What is the accumulated delivery fee for? How much is the handling fee?</h5>
                          <p>The flat-rate handling fee is <span>S$5.99</span> and it is only applicable to normal samples. For free samples, they are fully paid for and there are no additional charges to deliver the free samples.</p>
                          <p>Handling fee covers the delivery, material, labour and logistics cost to support the sampling service. You can redeem up to 4 different samples in each checkout and it is likely that you will find samples bundle with larger supply (up to 1-week supply) on Samplestore.com.</p>
                          <h5>How do you ship my orders?</h5>
                          <p>All your orders are sent via <a href="http://www.singpost.com.sg" target="_blank" rel="noopener noreferrer">Singapore Post Limited (SingPost)</a>.</p>
                          <h5>How do I make payments using Paypal? How does it work?</h5>
                          <p>Paypal is the easiest way to make payments online. While checking out your order, you will be redirected to the Paypal website. Be sure to fill in correct details for fast &amp; hassle-free payment processing. After a successful Paypal payment, a payment advice will be automatically generated to Samplestore.com system for your order.</p>
                          <p>It's fast, easy &amp; secure.</p>
                          <h5>What are the payment methods available?</h5>
                          <p>At the moment, we only accept Credit/Debit cards and Paypal payments.</p>
                          <h5>Can I pay using Paypal without a Paypal account?</h5>
                          <p>Yes! It is commonly misunderstood that a Paypal account is needed in order to make payments through Paypal. The truth is you DO NOT need one, although we strongly recommend you sign up to enjoy the added ease of use.</p>
                          <p>Without a Paypal account, all you need is any Debit/Credit card stated below that is supported by Paypal.</p>
                          <p>By using Paypal, we can process &amp; deliver your orders to you in a shorter time. Paypal is the easiest &amp; most secure way to make payment online. No account needed.</p>
                          <p>For more details, <a href="https://www.paypal.com/sg/webapps/mpp/how-paypal-works" target="_blank" rel="noopener noreferrer">click here</a> to see how Paypal works for you.</p>
                        </div>
                        <div className="col-half-right">
                          <h5>How can I use my remaining Account Credits?</h5>
                          <p>We are in the process of removing the option to pay for your orders by Account Credits. If you have remaining credits in your account, it will be used to pay for your next checkout. If there are insufficient credits, the system will direct you automatically to pay the balance via Paypal.</p>
                          <h5>Why must I make payment immediately at checkout?</h5>
                          <p>Sample ordering is on first-come-first-served basis. To ensure that you get your desired samples, it is recommended that you make your payment within 60 minutes of checking out.</p>
                          <h5>How many free samples can I redeem?</h5>
                          <p>Due to the limited quantity, each member's account is only entitled to 1 unique free sample. You can check out up to 4 free samples in each checkout.</p>
                          <h5>What happens if there's been a delivery mishap to my order? (Damaged or lost delivery)</h5>
                          <p>We take such matters very seriously and will look into individual cases thoroughly. Any sample that falls under the below categories should not be thrown away before taking photo proof and emailing the photo of the affected sample and your D.O (Delivery Order) to us at help@samplestore.com (if applicable).</p>
                          <p>We regret to inform you that no refunds will be given for orders that fall under the below categories.</p>
                          <p>1. In the event of damaged samples received, we will require photo proof of the affected samples and your D.O (Delivery Order) in order for us to investigate and review before a decision is made to re-send the sample to you at no cost, subject to availability. In light of this, any sample that falls into this category should not be thrown away before taking photo proof and emailing the photo to us at help@samplestore.com</p>
                          <p>2. In the event of lost mail, we will try to locate the delivery team in Singpost and if there's a clear indication that your order is indeed lost, we'll re-send the order to you at no cost, subject to availability.</p>
                          <h5>What happens in the event of unsatisfactory/expired/wrong sample/missing samples?</h5>
                          <p>We take such matters very seriously and will look into individual cases thoroughly. Any sample that falls under the below categories should not be thrown away before taking photo proof and emailing the photo of the affected sample and your D.O (Delivery Order) to us at help@samplestore.com (if applicable).</p>
                          <p>We regret to inform you that no refunds will be given for orders that fall under the below categories.</p>
                          <p>1. In the event that the sample you've received is unsatisfactory in any way you perceive, we will require photo proof of the sample and your D.O (Delivery Order) as well and you may be required to send us back the sample for close inspection and review before a decision is made to re-send a sample to you at no cost, subject to availability. The postage cost will be credited back to your account after we receive the returned item.</p>
                          <p>2. In the event that you receive an expired product, we will require clear photo proof of the sample and its expiry date for close inspection and review before a decision is made to re-send a sample to you at no cost, subject to availability.</p>
                          <p>3. In the event that you've received the wrong sample, we will require photo proof of the wrongly sent sample and D.O (Delivery Order) and after reviewing, we'll re-send the correct sample to you at no cost, subject to availability.</p>
                          <p>4. In the event you've received your order with a missing sample, we will require you to email us a clear photo proof of your D.O (Delivery Order) to help@samplestore.com and after which, kindly give us a call at (+65) 68440092 and our customer service officer will attend to you to find out more before a decision is made to re-send the missing sample to you at no cost, subject to availability.</p>
                          <h5>I am having problems accessing Sample Store. Some of the pages look weird. Am I using the right browser?</h5>
                          <p>As Sample Store uses some of the latest graphics designs which may not be supported in lower version of browsers, it is recommended that you use the following browsers to access Sample Store:</p>
                          <p>1. Microsoft Internet Explorer Version 10 onwards.</p>
                          <ul>
                            <li>Download the latest Microsoft Internet Explorer at: <a href="http://windows.microsoft.com/en-us/internet-explorer/download-ie">http://windows.microsoft.com/en-us/internet-explorer/download-ie </a></li>
                          </ul>
                          <p>2. Mozilla Firefox Version 10 onwards.</p>
                          <ul>
                            <li>Download the latest Mozilla Firefox at: <a href="https://www.mozilla.org/en-US/firefox/new/">https://www.mozilla.org/en-US/firefox/new/ </a></li>
                          </ul>
                          <p>3. Google Chrome Version 12 onwards.</p>
                          <ul>
                            <li>Download the latest Google Chrome at: <a href="https://www.google.com/chrome/browser/desktop/">https://www.google.com/chrome/browser/desktop/ </a></li>
                          </ul>
                          <p>In addition, please ensure that your Javascript and Cookie is enabled on your browser.</p>
                        </div>
                        <div className="clear">&nbsp;</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              </div>
            </div>
          </main>
        </div>
      </Bounce>
    )
  }
}

export default Faq;